import React from "react";
import { Route, Switch } from "react-router-dom";
import HomePage from "./home/HomePage";
import AboutPage from "./about/AboutPage";
import Header from "./common/Header";
import PageNotFound from "./PageNotFound";
import SportLeaguesPage from "./sportleagues/SportLeaguesPage";
import ManageSportLeaguePage from "./sportleagues/ManageSportLeaguePage"; // eslint-disable-line import/no-named-as-default
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function App() {
  return (
    <div className="container-fluid">
      <Header />
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route path="/about" component={AboutPage} />
        <Route path="/sportleagues" component={SportLeaguesPage} />
        <Route path="/sportleague/:id" component={ManageSportLeaguePage} />
        <Route path="/sportleague" component={ManageSportLeaguePage} />
        <Route component={PageNotFound} />
      </Switch>
      <ToastContainer autoClose={3000} hideProgressBar />
    </div>
  );
}

export default App;
